p {
  margin: 0;
  padding: 0;
}

._container {
  max-width: 1600px;
  margin: 0 auto;
}

button {
  cursor: pointer;
  background-color: transparent;
  border: 0;
}

@media (max-width: 1600px) {
  ._container {
    max-width: none;
    padding: 0 15px;
  }
}

.navigation {
  position: fixed;
  z-index: 10;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  opacity: 0;
  visibility: hidden;
  height: 60px;
  &__list {
    display: flex;
    justify-content: space-between;
    gap: 10px;
    margin: 0;
    height: 100%;
  }

  &__item {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    width: 60px;
    &:last-child {
      background-color: #b02929;
    }
  }
  @media (max-width: 479.98px) {
    opacity: 1;
    visibility: visible;
  }
}

.modal-dialog-centered {
  margin: 0 auto;
  @media (max-width: 380px) {
    max-width: auto;
    width: 90%;
  }
}

.h2 {
  @include adaptiv-font(40, 22, 1);
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  font-family: Druk Text Wide;
  margin: 0;
  padding: 0;
  line-height: normal;
  span {
    color: #b02929;
  }
}

.h3 {
  @include adaptiv-font(27, 20, 1);
  span {
    color: #b02929;
  }
}

.head-logo-bg {
  @media (max-width: 768.98px) {
    background: #333;
  }
}

.intro {
  @media (min-width: 768.98px) {
    background: url('../img/intro-bg.png') no-repeat center/cover;
  }

  position: relative;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 0;
    background: linear-gradient(288deg, #000 0.98%, rgba(0, 0, 0, 0) 34.09%);
  }
  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 0;
    background: linear-gradient(94deg, #000 26.41%, rgba(0, 0, 0, 0) 52.15%);
  }

  @media (max-width: 768.98px) {
    &::before,
    &::after {
      display: none;
    }
  }

  ._container {
    &.bg {
      @include adaptiv-value('padding-top', 130, 45, 1);
      @include adaptiv-value('padding-bottom', 70, 30, 1);
      @media (max-width: 768.98px) {
        // background: url('../img/intro-bg.png') no-repeat center/cover;
        background: url('../img/intro-bg.png');
        background-repeat: no-repeat;
        background-position: 75% 20px;
        background-size: cover;
        position: relative;
        &::before {
          content: '';
          position: absolute;
          background: linear-gradient(
            180deg,
            #000 35.61%,
            rgba(0, 0, 0, 0) 69.89%
          );
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          z-index: 0;
        }
      }
    }
  }

  &__body {
    position: relative;
    z-index: 1;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    z-index: 1;
  }

  &__title {
    max-width: 900px;
    @include adaptiv-value('max-width', 900, 400, 1);
    @include adaptiv-font(60, 19, 1);
    span {
      margin-top: 25px;
      display: inline-block;
    }
  }

  &__transportation {
    display: inline-flex;
    gap: 10px;
    border: 4px solid #b02929;
    margin-top: 20px;
    span {
      @include adaptiv-font(27, 17, 1);
      font-weight: 400;
      line-height: normal;
      text-transform: uppercase;
      color: #fff;
      font-family: Druk Text Wide;
    }
    @media (max-width: 998px) {
      flex-direction: column;
      gap: 0;
    }
    @media (max-width: 479.98px) {
      margin: 20px auto 0 auto;
    }
  }

  &__transportation-row {
    display: flex;
    align-items: center;
    gap: 20px;
    padding: 20px;
    span {
      position: relative;
      &::after {
        content: '';
        position: absolute;
        left: 100%;
        top: 50%;
        width: 3px;
        height: 24px;
        background-color: #b02929;
        transform: translate(10px, -50%);
      }
    }
    @media (max-width: 998px) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
      gap: 0;
      padding: 0;
      position: relative;
      span {
        padding: 20px 0;
        text-align: center;
        border-bottom: 1px solid #b02929;
        &::after {
          display: none;
        }
      }
      &::before {
        content: '';
        position: absolute;
        top: 0;
        height: 100%;
        width: 2px;
        background-color: #b02929;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }

  &__transportation-block {
    background-color: #b02929;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    @media (max-width: 479.98px) {
      padding: 20px 10px;
    }
  }

  &__button {
    display: flex;
    background-color: #b02929;
    @include adaptiv-value('padding', 40, 20, 1);
    @include adaptiv-value('gap', 40, 20, 1);
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    span {
      font-size: 16px;
      font-weight: 400;
      letter-spacing: 0.48px;
      text-transform: uppercase;
      color: #fff;
      font-family: Druk Text Wide;
    }
    svg {
      width: 20px;
      height: 20px;
    }
    @media (max-width: 479.98px) {
      margin: 240px auto 0 auto;
    }
  }

  &__list {
    border-top: 4px solid #fff;
    width: 100%;
    @media (max-width: 768.98px) {
      background: #333;
    }
  }

  &__list-row {
    // display: grid;
    // grid-template-columns: repeat(4, 1fr);
    display: flex;
    justify-content: space-between;
    @media (max-width: 767px) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__item {
    width: 25%;
    min-height: 225px;
    border-right: 4px solid #fff;
    padding: 20px;
    background: linear-gradient(241deg, #000 -6.72%, #303030 110.52%);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    row-gap: 10px;
    &:first-child {
      border-left: 4px solid #fff;
    }
    &._active {
      background: #b02929;
      span {
        color: #fff;
      }
    }
    @media (max-width: 767px) {
      width: auto;
      max-width: 500px;
      border-left: 4px solid #fff;
      border-bottom: 4px solid #fff;
      &:last-child {
        border-bottom: none;
      }
    }
  }
  &__item-title {
    font-family: Druk Text Wide;
    color: #fff;
    @include adaptiv-font(27, 12, 1);
    font-weight: 400;
    line-height: 33px;
    text-transform: uppercase;
    span {
      color: #b02929;
    }
    @media (max-width: 767px) {
      font-size: 22px;
    }
  }

  &__item-text {
    color: #fff;
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
  }
}

