.transport {
  &__container {
  }

  &__body {
  }

  &__title {
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    @media (max-width: 998px) {
      grid-template-columns: repeat(2, 1fr);
      @include adaptiv-value('row-gap', 30, 10, 1);
    }
    @media (max-width: 575px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__block {
    width: 100%;
    border: 1px solid #ddd;
    border-bottom: none;
    display: flex;
    flex-direction: column;
    @media (min-width: 998px) {
      &:not(:last-child) {
        border-right: none;
      }
    }
    @media (max-width: 998px) {
      width: auto;
      max-width: 400px;
      margin: 0 auto;
    }
  }

  &__block-wpImg {
    display: flex;
    align-items: center;
    justify-content: center;
    @include adaptiv-value('min-height', 500, 245, 1);
    @include adaptiv-value('max-height', 500, 245, 1);
    img {
      max-width: 100%;
      max-height: 100%;
      object-fit: cover;
    }
  }

  &__block-content {
    display: flex;
    flex-direction: column;
    padding: 30px 10px 0 10px;
    flex: 1 1 auto;
  }

  &__block-title {
    width: 100%;
    color: #333;
    text-align: center;
    font-family: Druk Text Wide;
    @include adaptiv-font(18, 15, 1);
    font-weight: 700;
    @include adaptiv-value('line-height', 22, 17, 1);
    text-transform: uppercase;
  }

  &__block-text {
    flex: 1 1 auto;
    color: #333;
    text-align: center;
    font-family: Montserrat;
    font-size: 20px;
    @include adaptiv-value('line-height', 27, 22, 1);
    font-style: normal;
    font-weight: 400;
    margin-bottom: 10px;
  }

  &__block-btn {
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    padding: 28px 0;
    transition: 0.2s;
    &:hover {
      text-decoration: none;
    }
    span {
      color: #b02929;
      font-family: Druk Text Wide;
      font-size: 16px;
      font-weight: 400;
      letter-spacing: 0.48px;
      transition: 0.2s;
      text-transform: uppercase;
    }
    svg {
      width: 15px;
      height: 15px;
      transition: 0.2s;
      stroke: #b02929;
    }
    &:hover {
      background-color: #b02929;
      span {
        color: #fff;
      }
      svg {
        stroke: #fff;
      }
    }
  }
}

