.guarantee {
  @include adaptiv-value('margin-top', 100, 50, 1);
  // &__container {}

  // &__body {}

  // &__title {}

  &__content {
    @include adaptiv-value('margin-top', 50, 30, 1);
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
    @include adaptiv-value('gap', 30, 20, 1);
    @media (max-width: 998px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 576px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__block {
    position: relative;
    @include adaptiv-value('padding-top', 45, 35, 1);
    @include adaptiv-value('padding-bottom', 45, 35, 1);
    border-top: 2px solid #ddd;
    border-bottom: 2px solid #ddd;
    .plus {
      position: absolute;
      width: 20px;
      height: 20px;
      &::before,
      &::after {
        content: '';
        position: absolute;
        background-color: #ddd;
      }

      &::before {
        width: 100%;
        height: 2px;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
      &::after {
        width: 2px;
        height: 100%;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
      }

      &:nth-child(1) {
        left: 0;
        top: 0;
        transform: translateY(10px);
      }
      &:nth-child(2) {
        right: 0;
        top: 0;
        transform: translateY(10px);
      }
      &:nth-child(3) {
        left: 0;
        bottom: 0;
        transform: translateY(-10px);
      }
      &:nth-child(4) {
        bottom: 0;
        right: 0;
        transform: translateY(-10px);
      }
    }
  }

  &__block-text {
    color: #333;
    font-family: Druk Text Wide;
    font-size: 20px;
    font-weight: 400;
    line-height: 25px;
    text-transform: uppercase;
    text-align: center;
    span {
      color: #b02929;
    }
  }
}

.glk {
