      border-bottom: none;
    }
  }

  &__wpImg {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: calc((100% - 1600px) / 2);
    flex-shrink: 0;
    min-width: 510px;
    img {
      max-width: 100%;
      max-height: 100%;
      object-fit: cover;
    }
    @media (max-width: 1600px) {
