    @include adaptiv-value('padding-left', 45, 20, 1);
    @include adaptiv-value('padding-right', 45, 20, 1);
    &:first-child {
      border-top: 2px solid #ddd;
    }
    svg {
      flex-shrink: 0;
      @include adaptiv-value('width', 25, 15, 1);
      @include adaptiv-value('height', 25, 15, 1);
    }
  }

  &__item-block {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__item-title {
    color: #333;
    font-family: Montserrat;
    font-weight: 700;
    @include adaptiv-value('line-height', 36, 21, 1);
    @include adaptiv-font(27, 17, 1);
