    display: flex;
    flex-direction: column;
    align-items: center;
    @include adaptiv-value('row-gap', 30, 10, 1);
  }

  &__swiper-wpImg {
    width: 100%;
    @include adaptiv-value('min-height', 600, 450, 1);
    @include adaptiv-value('max-height', 600, 450, 1);
