    align-items: center;
    justify-content: center;
    overflow: hidden;
    img {
      max-width: 100%;
      max-height: 100%;
      object-fit: contain;
    }
  }

  &__swiper-text {
    color: #000;
    text-align: center;
    font-family: Druk Text Wide;
    @include adaptiv-font(20, 17, 1);
    font-weight: 700;
    line-height: normal;
    text-transform: uppercase;
    max-width: 350px;
