      align-items: center;
      justify-content: center;
    }
  }

  &__swiper-pagination {
    position: relative;
    margin-top: 20px;
    display: none;
    .swiper-pagination-bullet {
      border-radius: 0;
      height: 5px;
      background-color: #333;
      transition: 0.2s;
      opacity: 1;
      @include adaptiv-value('height', 7, 5, 1);
      @include adaptiv-value('width', 16, 10, 1);
      &.swiper-pagination-bullet-active {
        @include adaptiv-value('width', 20, 14, 1);
