.director {
  @include adaptiv-value('margin-top', 100, 50, 1);
  &__title {
    display: block;
    margin: 0 auto;
    text-align: center;
    margin: 0 auto;
    padding: 0 15px;
    svg {
      transform: translateY(-4px);
      @include adaptiv-value('width', 172, 115, 1);
      @include adaptiv-value('height', 30, 20, 1);
    }
  }

  &__body {
    border-top: 2px solid #ddd;
    border-bottom: 2px solid #ddd;
    @include adaptiv-value('margin-top', 40, 30, 1);
    @media (max-width: 998px) {
      border: none;
    }
  }

  // &__container { }

  &__content {
    display: flex;
    border-right: 2px solid #ddd;
    border-left: 2px solid #ddd;
    @media (max-width: 998px) {
      flex-direction: column;
      align-items: center;
      border: none;
      row-gap: 15px;
    }
  }

  &__left {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: calc(600 * 100% / 1600);
    padding: 20px;
    border-right: 2px solid #ddd;
    @media (max-width: 998px) {
      // flex: 1 1 100%;
      width: 100%;
      border: none;
      padding: 0;
    }
  }

  &__wpImg {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 720px;
    background: #f8f8f8;
    overflow: hidden;
    width: 100%;
    border: 1px solid #ddd;
    img {
      max-width: 100%;
      max-height: 100%;
      object-fit: contain;
    }
    @media (max-width: 1600px) {
      @include adaptiv-value('min-height', 720, 450, 1);
      @include adaptiv-value('max-height', 720, 450, 1);
    }
    @media (max-width: 998px) {
      max-width: 400px;
    }
  }

  &__name {
    margin-top: 20px;
    color: #000;
    text-align: center;
    font-family: Druk Text Wide;
    font-size: 27px;
    font-weight: 700;
    line-height: normal;
    text-transform: uppercase;
    @include adaptiv-font(27, 20, 1);
  }

  &__left-span {
    color: #b02929;
    text-align: center;
    font-family: Druk Text Wide;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
  }

  &__right {
    width: calc(1000 * 100% / 1600);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    row-gap: 15px;
    @include adaptiv-value('padding', 60, 0, 1);
    @media (max-width: 998px) {
      border-top: 2px solid #ddd;
      border-bottom: 2px solid #ddd;
      width: 100%;
      padding: 30px 0;
    }
  }

  &__right-span {
    color: #000;
    text-align: center;
    font-family: Druk Text Wide;
    font-size: 27px;
    font-weight: 700;
    line-height: normal;
    text-transform: uppercase;
    @include adaptiv-font(27, 20, 1);
  }

  &__right-text {
    color: #333;
    font-family: Montserrat;
    font-weight: 400;
    line-height: 36px;
    @include adaptiv-value('line-height', 36, 24, 1);
    @include adaptiv-font(27, 16, 1);
  }
}

.questions {
  @include adaptiv-value('margin-top', 100, 50, 1);
  @include adaptiv-value('margin-bottom', 100, 50, 1);
  // &__container {}

  // &__title {}
  .send-form {
    padding: 0;
  }

  &__subt-title {
    margin-top: 20px;
    color: #333;
    text-align: center;
    font-family: Montserrat;
    font-weight: 400;
    @include adaptiv-value('margin-bottom', 50, 30, 1);
    @include adaptiv-value('line-height', 36, 24, 1);
    @include adaptiv-font(27, 18, 1);
  }
  .nopad {
    @media (max-width: 768.98px) {
      margin: 0;
    }
  }
  .btn {
    @media (max-width: 479.98px) {
      width: 100%;
      margin: 0 auto;
    }
  }
}

.footer {
  background-color: #333;
  ._container {
    @media (max-width: 1600px) {
      padding: 0;
    }
  }
  // &__top {}

  &__list {
    display: flex;
    margin: 0;
  }

  &__list-top {
    @media (max-width: 768.98px) {
      padding-top: 10px;
    }
  }

  &__cell {
    width: 25%;
    display: flex;
    align-items: center;
    justify-content: center;
    @include adaptiv-value('height', 100, 50, 1);
    padding: 5px;
    border-right: 2px solid #ddd;
    &:first-child {
      border-left: 2px solid #ddd;
    }
  }

  &__cell-top {
    a {
      color: #fff;
      font-family: Montserrat;
      font-weight: 400;
      text-transform: uppercase;
      @include adaptiv-font(27, 16, 1);
    }
    @media (max-width: 998px) {
      &:nth-child(1),
      &:nth-child(2) {
        width: 50%;
      }
      &:nth-child(3),
      &:nth-child(4) {
        display: none;
      }
    }
    @media (max-width: 768.98px) {
      border-top: 2px solid #ddd;
    }
  }

  &__body {
    border-top: 2px solid #ddd;
    border-bottom: 2px solid #ddd;
  }

  &__content {
    @include adaptiv-value('padding-top', 100, 60, 1);
    @include adaptiv-value('padding-bottom', 80, 20, 1);
    display: flex;
    @media (max-width: 768.98px) {
      flex-direction: column;
      align-items: center;
      row-gap: 40px;
    }
  }

  &__col {
    width: 25%;
    padding-right: 10px;
    .block-m {
      margin-top: 20px;
      display: none;
      @media (max-width: 479.98px) {
        display: block;
      }
    }

    .link-m {
      display: flex;
      gap: 10px;
      border-bottom: 1px solid #ddd;
      svg {
        width: 28px;
        height: 28px;
      }
      span {
        color: #fff;
        font-family: Druk Text Wide;
        font-size: 14px;
        font-weight: 400;
        letter-spacing: 0.48px;
        text-transform: uppercase;
        padding: 20px 0;
      }
      &:first-child {
        border-top: 1px solid #ddd;
      }
    }
    .wp-svg {
      width: 60px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #b02929;
    }
    @media (max-width: 998px) {
      &:nth-child(3) {
        display: none;
      }
      &:nth-child(4) {
        width: 50%;
      }
    }
    @media (max-width: 768.98px) {
      padding: 0;
      width: 100%;
      &:nth-child(4) {
        width: 100%;
      }
      text-align: center;
    }
  }

  &__logo {
    width: 100%;
    img {
      width: 172px;
      height: 30px;
    }
  }

  &__col-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include adaptiv-value('gap', 50, 20, 1);
    @media (max-width: 768.98px) {
      align-items: center;
    }
  }

  &__col-li {
    a {
      color: #ddd;
      font-family: Montserrat;
      font-weight: 700;
      text-transform: uppercase;
      @include adaptiv-font(20, 14, 1);
      @media (max-width: 768.98px) {
        font-size: 20px;
      }
    }
  }

  &__tell {
    color: #fff;
    font-family: Druk Text Wide;
    font-weight: 700;
    text-transform: uppercase;
    transition: 0.2s;
    @include adaptiv-font(27, 10, 1);
    &:hover {
      color: #b02929;
    }
    @media (max-width: 998px) {
      font-size: 25px;
    }
    @media (max-width: 768.98px) {
      width: 100%;
      text-align: center;
      margin: 0 auto;
      display: inline-block;
    }
  }

  &__button {
    background: #b02929;
    color: #fff;
    width: 100%;
    font-family: Druk Text Wide;
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.48px;
    text-transform: uppercase;
    @include adaptiv-value('padding-top', 40, 20, 1);
    @include adaptiv-value('padding-bottom', 40, 20, 1);
    @include adaptiv-value('margin-top', 30, 12, 1);
    @include adaptiv-font(16, 12, 1);
    svg {
      width: 20px;
    }
    @media (max-width: 998px) {
      font-size: 16px;
    }
    @media (max-width: 479.98px) {
      padding: 40px 0;
    }
  }

  &__bottom {
  }

  &__list-bottom {
    @media (max-width: 768.98px) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__cell-bottom {
    span {
      color: #ddd;
      font-family: Montserrat;
      font-size: 12px;
      font-weight: 400;
      letter-spacing: 0.36px;
      text-transform: uppercase;
      @include adaptiv-font(12, 10, 1);
      text-align: center;
    }
    &:first-child {
      span {
        font-weight: bold;
      }
    }
    @media (max-width: 768.98px) {
      width: 100%;
      border-bottom: 2px solid #ddd;
      &:nth-child(3) {
        border-left: 2px solid #ddd;
      }
    }
    @media (max-width: 479.98px) {
      &:first-child {
        span {
          color: #b02929;
        }
      }
    }
  }
}
