@font-face {
  font-family: 'Druk Text Wide';
  src: url('../fonts/DrukTextWide-Medium.woff2') format('woff2'),
    url('../fonts/DrukTextWide-Medium.woff') format('woff'),
    url('../fonts/DrukTextWide-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Druk Text Wide';
  src: url('../fonts/DrukTextWide-Bold.woff2') format('woff2'),
    url('../fonts/DrukTextWide-Bold.woff') format('woff'),
    url('../fonts/DrukTextWide-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

.btn-primary:not(:disabled):not(.disabled).active:focus,
.btn-primary:not(:disabled):not(.disabled):active:focus,
.show > .btn-primary.dropdown-toggle:focus {
  box-shadow: none;
}

html {
  line-height: 12px;
  font-family: $font-family-base;
  font-weight: 500;
}

body {
  overflow-x: hidden;
}

a {
  font-family: $font-family-base;
  color: $primary;

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

P {
  margin-bottom: 1rem;
}

i {
  font-weight: normal;
  font-style: normal;
}

h1 {
  font-family: $font-family-header;
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 125%;
  text-transform: uppercase;
  color: #ffffff;
  margin-bottom: 0;
}

h2 {
  font-family: $font-family-header;
  font-style: normal;
  font-weight: bold;
  font-size: 27px;
  line-height: 155%;
  text-align: center;
  text-transform: uppercase;
  color: #333333;
  padding: 0 10px;
}

.btn {
  background: $red;
  border: 0;
  color: #fff;
  font-family: $font-family-header;
  font-size: 16px;
  line-height: 115%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  min-height: 100px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0;
  padding: 16px 36px;

  i {
    width: 20px;
    height: 20px;
    min-width: 20px;
    min-height: 20px;
  }
}

ul {
  list-style: none;
  padding-left: 0;
}

img {
  max-width: 100%;
  height: auto;
}

.form-control::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #333333;
  opacity: 1;
}

.form-control::-moz-placeholder {
  /* Firefox 19+ */
  color: #333333;
  opacity: 1;
}

.form-control:-ms-input-placeholder {
  /* IE 10+ */
  color: #333333;
  opacity: 1;
}

.form-control:-moz-placeholder {
  /* Firefox 18- */
  color: #333333;
  opacity: 1;
}

.red {
  color: $red;
}

.form-row {
  margin: 0;
}

.modal-content {
  border-radius: 0;

  .send-form {
    padding: 0;

    .btn {
      padding: 41px 20px;
      font-size: 16px;
    }
  }
}

.modal-dialog {
  max-width: 360px;
}

.modal-header {
  padding-top: 30px;
  border: 0;
}

.modal-title {
  font-family: $font-family-header;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 135%;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}

.modal-body {
  padding-left: 10px;
  padding-right: 10px;
}

.form-row > .col,
.form-row > [class*='col-'] {
  padding: 0;
}

.header-main {
}

.header-main-bg {
  position: relative;
  margin-top: 20px;
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 50px;
}

.header-main-wrap {
  position: relative;
  background: #000;
  max-width: 900px;
  span {
    display: inline-block;
    margin-top: 25px;
  }
}

.head-logo-bg {
  background: #000;
}

.logo {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.head_mobile {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  min-height: 80px;
  background: $red;
  padding: 0 20px;

  .phone {
    margin-bottom: 10px;
    display: block;
    white-space: nowrap;
  }
}

.phone {
  font-family: $font-family-base;
  font-style: normal;
  font-weight: bold;
  font-size: rem(15px);
  line-height: 135%;
  text-transform: uppercase;
  color: #fff;
}

.callme {
  font-family: $font-family-header;
  font-size: rem(12px);
  line-height: 115%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #fff;
  display: flex;
  min-height: auto;
  padding: 0;
  justify-content: flex-start;
  text-align: left;
}

.h1_top {
  font-family: Druk Text Wide;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 135%;
  padding: 0 10px;
  text-transform: uppercase;
  color: #fff;
}

.header-main-blocks {
  position: relative;
  z-index: 1;
  //   background: #000;
  padding: 30px 0 0;
  color: #fff;

  .row > div {
    border-top: 2px solid #fff;
    padding: 0 10px;
  }

  .white-box {
    border-right: 2px solid #fff;
    border-left: 2px solid #fff;
    padding: 22px 10px;
    height: 225px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    background: linear-gradient(241.45deg, #000000 -6.72%, #303030 110.52%);

    .title {
      font-family: Druk Text Wide;
      font-style: normal;
      font-weight: normal;
      font-size: 27px;
      line-height: 125%;
      text-transform: uppercase;
    }

    .caption {
      font-family: Montserrat;
      font-style: normal;
      font-weight: bold;
      font-size: 16px;
      line-height: 137.5%;
    }
  }
}

.bgred .white-box {
  background: $gradient;
}

.transportation-block {
  display: inline-flex;
  border: 4px solid #b02929;
  gap: 20px;
  color: #fff;
  font-weight: 400;
  font-size: 27px;
  text-transform: uppercase;
  p {
    display: flex;
    align-items: center;
    padding: 20px;
    gap: 20px;
    margin-bottom: 0;
  }
  span {
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: 100%;
      top: 50%;
      width: 3px;
      height: 24px;
      transform: translate(10px, -50%);
      background-color: #b02929;
    }
  }

  div {
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #b02929;
  }
}

.header-main-btn {
  width: 100%;
}

.section h2 {
  padding: 50px 0 0;
  margin-bottom: 30px;
}

.gray-box {
  .title {
    font-family: $font-family-header;
    font-size: 20px;
    line-height: 125%;
    text-transform: uppercase;
    color: #b02929;
    text-align: center;
  }

  .img {
    overflow: hidden;
  }

  img {
    margin-top: -80px;
  }

  .list-arrow {
    li {
      &:first-child {
        font-family: $font-family-header;
        font-size: 15px;
        line-height: 115%;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        color: #000000;

        .arrow-inline {
          justify-content: center;
        }

        i {
          display: none;
        }
      }
    }
  }
}

.img,
.img img {
  max-width: 100%;
  height: auto;
}

.list-arrow {
  padding: 0 10px;

  li {
    padding: 20px 0;
    border-bottom: 1px solid #dddddd;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 115%;
    letter-spacing: 0.03em;
    color: #333333;
  }
}

.white-arrow {
  background: url(../img/arrow_white.svg) no-repeat 0 0;
  width: 15px;
  height: 15px;
  margin-left: 18px;
  background-size: contain;
  display: block;
  min-width: 15px;
  min-height: 15px;
}

.red-arrow {
  background: url(../img/arrow_red.svg) no-repeat 0 0;
  width: 15px;
  height: 15px;

  background-size: contain;
  display: block;
  min-width: 15px;
  min-height: 15px;
}

.arrow-left {
  margin-right: 15px;
}

.arrow-inline {
  display: flex;
  align-items: center;

  .active {
    font-family: $font-family-header;
    font-size: 16px;
    line-height: 115%;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: #b02929;
  }
}

.arrow-right {
  margin-left: 20px;
}

.send-form {
  padding: 10px;
}

.form-control {
  background: #f8f8f8;
  border: 1px solid #dddddd;
  font-family: $font-family-header;
  font-size: 16px;
  line-height: 115%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #333333;
  border-radius: 0;
  padding: 24px;
  height: auto;
  text-align: center;
}

.nopad {
  width: calc(100% + 20px);
  margin: 0 -10px;
  justify-content: center;
  display: flex;
}

.btn-center {
  width: 100%;
}

.blocks {
  .block {
    padding: 10px;
    margin-bottom: 20px;
  }

  .block-inner {
    padding: 20px 0;
    border-top: 2px solid #dddddd;
    border-bottom: 2px solid #dddddd;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      background: url(../img/plus.svg) no-repeat 0 0;
      right: 0;
      bottom: 10px;
      width: 20px;
      height: 20px;
    }

    &::before {
      content: '';
      position: absolute;
      background: url(../img/plus.svg) no-repeat 0 0;
      left: 0;
      bottom: 10px;
      width: 20px;
      height: 20px;
    }
  }

  .plus-top-l {
    position: absolute;
    background: url(../img/plus.svg) no-repeat 0 0;
    left: 0;
    top: 10px;
    width: 20px;
    height: 20px;
  }

  .plus-top-r {
    position: absolute;
    background: url(../img/plus.svg) no-repeat 0 0;
    right: 0;
    top: 10px;
    width: 20px;
    height: 20px;
  }

  .title {
    font-family: $font-family-header;
    font-style: normal;
    font-weight: normal;
    font-size: 34px;
    line-height: 125%;
    text-align: center;
    text-transform: uppercase;
    color: #333333;
    max-width: 300px;
    margin: 0 auto 20px auto;
  }

  .text {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 135%;
    text-align: center;
    color: #333333;
    padding: 0 20px;
  }
}

.blocks-cifra {
  .block {
    padding: 10px 35px;
  }

  .block-inner {
    height: 240px;

    display: flex;
    justify-content: center;
    flex-direction: column;
  }

  .plus-top-l {
    background: none;
    font-family: $font-family-header;
    font-size: 37px;
    line-height: 135%;
    text-align: center;
    color: #dddddd;
  }

  .img {
    text-align: center;
    margin-bottom: 30px;

    img {
      width: fit-content !important;
      display: inline-block !important;
    }
  }

  .block-inner {
    padding: 30px 50px 50px 50px;
  }
}

.left-block {
  img {
    margin-bottom: 40px;
  }

  .caption {
    font-weight: 500;
    font-size: 18px;
    line-height: 137.5%;
    color: #333333;
    padding: 0 10px;
    margin-bottom: 10px;
  }

  .list-bigarrow {
    font-family: $font-family-header;
    font-size: 20px;
    line-height: 125%;
    text-align: center;
    text-transform: uppercase;
    color: #333333;

    li {
      border-top: 1px solid #dddddd;
      padding: 15px 0;
      display: flex;
      justify-content: center;
      text-align: left;

      &:last-child {
        border-bottom: 1px solid #dddddd;
      }

      i {
        display: none;
      }
    }
  }
}

.right-block {
  display: none;
}

.block-gray {
  padding: 0 0 30px 0;
  background: #f5f5f5;
  text-align: center;

  .title {
    text-align: center;
  }

  .title h3 {
    font-family: $font-family-header;
    font-size: 20px;
    line-height: 115%;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    font-style: normal;
    font-weight: normal;
    display: inline-block;

    &::after {
      content: '';
      width: auto;
      height: 2px;
      display: block;
      background: #b02929;
      margin-top: 10px;
    }
  }
}

.block-with-text .arrow-inline {
  padding-left: 40px;
}

.block-chess {
  border-top: 1px solid #dddddd;
  border-bottom: 1px solid #dddddd;
  text-align: center;

  .title {
    font-family: $font-family-header;
    font-size: 20px;
    line-height: 125%;
    text-align: center;
    text-transform: uppercase;
    color: #333333;
    padding-bottom: 10px;
    padding-top: 20px;
  }

  .caption {
    font-weight: 500;
    font-size: 18px;
    line-height: 135%;
    text-align: center;
    color: #333333;
    padding-bottom: 20px;
  }

  .title-wrap {
    border-bottom: 1px solid #ddd;
    padding: 10px;
  }
}

.owl-carousel {
  .owl-nav {
    position: absolute;
    top: 50%;
    width: 100%;
    transform: translate(0, -50%);

    button.owl-next,
    button.owl-prev {
      background: url(../img/arrow-slider.svg) no-repeat 50% 50%;
      width: 32px;
      height: 32px;

      span {
        display: none;
      }
    }

    button.owl-next {
      transform: rotate(180deg);
      right: 0;
      position: absolute;
    }
  }

  .owl-dots {
    text-align: center;
  }

  button.owl-dot {
    width: 10px;
    height: 5px;
    background: #333333;
    margin: 5px;

    &.active {
      background: $gradient;
    }
  }

  .owl-item img {
    width: auto;
    margin: 0 auto;
  }
}

.black {
  background: #333;
  color: #fff;

  a {
    color: #fff;
  }

  h2 {
    color: #fff;
    margin-bottom: 10px;
  }

  .send-form {
    padding-bottom: 0;
  }
}

.h2-sub {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 135%;
  text-align: center;
  color: #ffffff;
  margin-bottom: 10px;
}

.list-line-height {
  padding: 0;

  li {
    line-height: 135%;
    padding-left: 18px;
    padding-right: 18px;
  }
}

.sub {
  font-size: 18px;
  line-height: 135%;
  text-align: center;
  color: #333333;
}

.white-submit {
  .form-control {
    background: #fff;
  }
}

.footer-contacts-pad {
  padding: 10px 0;
  border-left: 1px solid #ffffff;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  a {
    color: #fff;
    font-family: Montserrat;
    font-weight: 400;
    text-transform: uppercase;
    @include adaptiv-font(27, 20, 1);
  }
}

.footer-contacts .col-6:first-child .footer-contacts-pad {
  border-left: 0;
}

.footer-contacts-wrap {
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
}

#footer {
  padding: 12px 0;

  .logo {
    padding: 30px 0 40px 0;
  }

  .logo img {
    width: 172px;
    height: 30px;
  }
}

.footer-list {
  font-weight: bold;
  font-size: 20px;
  line-height: 135%;
  text-transform: uppercase;
  color: #dddddd;
  padding: 0 10px;
  margin: 0;

  a {
    color: #dddddd;
  }

  li {
    padding-bottom: 30px;
  }
}

.foot-phone {
  text-align: left;

  a {
    // font-family: $font-family-header;
    font-family: Druk Text Wide;
    font-weight: bold;
    font-size: 27px;
    line-height: 135%;
    text-transform: uppercase;
    color: #ffffff;
    padding-bottom: 30px;
    display: inline-block;
  }
}

.copyright {
  border-top: 1px solid #ffffff;
  margin-top: 20px;

  .col-6:nth-child(2) {
    .ipad {
      border-right: 0;
    }
  }

  .col-6:nth-child(4) {
    .ipad {
      border-right: 0;
    }
  }
}

.ipad {
  padding: 18px 10px;

  font-size: 10px;
  line-height: 115%;
  letter-spacing: 0.03em;
  text-transform: lowercase;
  color: #dddddd;
  text-align: center;
  border-bottom: 1px solid #ffffff;
  height: 100%;
  border-right: 1px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;

  a {
    font-size: 10px;
    line-height: 115%;
    letter-spacing: 0.03em;
    text-transform: lowercase;
    color: #dddddd;
  }

  .copy {
    color: $red;
    text-transform: uppercase;
  }
}

.form-bot {
  padding-bottom: 30px;
}

.img-bg {
  height: 290px;
  background-size: cover;
  border-bottom: 1px solid #dddddd;
  background-size: cover;
  background-position: center bottom;
  background-repeat: no-repeat;
}

.img-bg-1 {
  background-image: url(../img/sposob1.jpg);
}

.img-bg-2 {
  background-image: url(../img/sposob2.jpg);
}

.img-bg-3 {
  background-image: url(../img/sposob3.jpg);
}

//MY MEDIA

@media (min-width: 576px) {
  .header-main-blocks .row > div {
    padding: 0;
  }

  .header-main {
    // background: #000;
  }

  .img-bg {
    height: 390px;
  }
}

@media (min-width: 768px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1600px;
  }

  .header-main {
    // background: url(../img/bg.jpg) no-repeat 50% 100% #000;
  }

  .header-main-bg {
    background: none;
  }

  .header-main-blocks {
    padding: 0;
  }

  .btn {
    padding: 20px 25px;
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 115%;
    background: linear-gradient(
      107.79deg,
      #b02929 -0.32%,
      #b70000 62.88%,
      #770000 102.43%
    );
    position: relative;
    text-decoration: none;
    max-width: 100%;
    min-height: 100%;

    &:hover,
    &:focus {
      text-decoration: none;
      color: #fff;
    }

    &:hover:after {
      opacity: 0;
    }

    &:active {
      background: #fff;
      color: $red;
    }

    &::after {
      content: '';
      position: absolute;
      left: 0px;
      top: 0px;
      right: 0px;
      bottom: 0px;
      background: $red;
      opacity: 1;
      transition: all 0.35s ease-in-out;
    }

    span,
    i {
      position: relative;
      z-index: 1;
      text-decoration: none;
    }
  }

  .callme {
    width: auto;
  }

  .head_mobile {
    align-items: flex-start;
  }

  .head_mobile .phone {
    font-size: 21px;
    line-height: 135%;
    padding: 15px 30px;
    margin-bottom: 0;
  }

  .head_mobile_text {
    display: flex;
    align-items: center;
    height: 100%;

    a:hover,
    a:focus {
      text-decoration: none;
    }
  }

  .head_mobile {
    background: none;
    padding: 0;
  }

  .logo {
    justify-content: flex-start;
    padding-left: 20px;
    img {
      width: 130px;
      height: auto;
    }
  }

  h1 {
    font-size: 40px;
    line-height: 125%;
  }

  .header-main-wrap {
    padding-top: 80px;
    background: none;
  }

  .h1_top {
    font-size: 21px;
    line-height: 135%;
    margin-bottom: 45px;
  }

  .header-main-bg {
    min-height: auto;
  }

  .header-main-btn {
    margin-bottom: 70px;
    max-width: 360px;
    padding-left: 10px;
  }

  .header-main-blocks .row > div {
    padding: 0;
  }

  .header-main-blocks .white-box {
    border-right: 1px solid #fff;
    border-left: 1px solid #fff;
  }

  .header-main-blocks {
    border-top: 2px solid #fff;
  }

  .header-main-blocks .row > div {
    border: 0;
  }

  .header-main-blocks .row {
    border-left: 1px solid #fff;
    border-right: 1px solid #fff;
  }

  .header-main-blocks .white-box .title {
    font-size: 16px;
  }

  .section h2 {
    padding: 50px 0 0;
    margin-bottom: 25px;
    font-size: 27px;
    line-height: 155%;
  }

  .transport-wrap {
    border-top: 2px solid #ddd;
    border-bottom: 2px solid #ddd;
  }

  .gray-box {
    border-right: 2px solid #ddd;
    height: 100%;

    .title {
      color: #333333;
      font-size: 21px;
      line-height: 125%;
      padding-top: 20px;
    }

    .arrow-inline {
      font-weight: bold;
      font-size: 16px;
      line-height: 115%;
