    position: relative;
  }

  .header-main::before {
    content: '';
    position: absolute;
    z-index: 0;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(94deg, #000 26.41%, rgba(0, 0, 0, 0) 52.15%);
  }

  .header-main::after {
    content: '';
    position: absolute;
    z-index: 0;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(288deg, #000 0.98%, rgba(0, 0, 0, 0) 34.09%);
