  .btn {
    padding: 40px 45px;
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 115%;
    background: linear-gradient(107.79deg, #b02929 -0.32%, #b70000 62.88%, #770000 102.43%);
    position: relative;
    text-decoration: none;
    max-width: 100%;
    min-height: 100%;
  }

  .btn:hover,
  .btn:focus {
    text-decoration: none;
    color: #fff;
  }

  .btn:hover:after {
    opacity: 0;
  }

  .btn:active {
    background: #fff;
    color: #B02929;
  }

  .btn::after {
    content: '';
    position: absolute;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
    background: #B02929;
    opacity: 1;
    transition: all 0.35s ease-in-out;
